import NavStyles from './styles/NavStyles';
import User from './User';

import Link from 'next/link';
const Nav = () => (
    <NavStyles>
        <User>
            {({data: {currentUser}}) => {
                console.log(currentUser);
                if (currentUser) return currentUser.name;
                return null;
                }}
        </User>
        <Link href="/products">
            <a>Shop</a>
        </Link>
        <Link href="/sell">
            <a>Sell</a>
        </Link>
        <Link href="/orders">
            <a>Orders</a>
        </Link>
        <Link href="/me">
            <a>Account</a>
        </Link>
        <Link href="/signup">
            <a>Signup</a>
        </Link>
    </NavStyles>
);

export default Nav;